@font-face {
  font-family: "AvenirNextLTPro-Regular";
  src: url("../assets/font/AvenirNextLTPro-Regular.otf") format("opentype");
}
@font-face {
  font-family: "AvenirNextLTPro-Demi";
  font-weight: 600;
  src: url("../assets/font/AvenirNextLTPro-Demi.otf") format("opentype");
}
@font-face {
  font-family: "AvenirNextLTPro-Bold";
  font-weight: 700;
  src: url("../assets/font/AvenirNextLTPro-Bold.otf") format("opentype");
}
img {
  max-width: 100%;
}

body {
  font-family: "AvenirNextLTPro-Regular";
  padding: 0;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

h2 {
  color: #000000;
}

.body__header {
  width: 100%;
  background-color: #323232;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .body__header {
    width: auto;
    padding-left: 4rem;
    font-size: 1.7rem;
    align-content: space-between;
    flex-direction: row;
  }
}
.body__header #navbar-logo {
  order: -1;
  width: 100%;
  color: #ffffff;
  font-size: 1.5rem;
  text-align: center;
  padding: 1rem;
  margin-right: auto;
  text-transform: uppercase;
  padding-top: 2rem;
  padding-bottom: 0;
}
@media (min-width: 768px) {
  .body__header #navbar-logo {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
}
.body__header #navbar-active {
  border-style: solid;
  border-width: 0;
  border-bottom-width: 0.25rem;
  border-color: #ffffff;
}
.body__header nav {
  width: 100%;
}
@media (min-width: 768px) {
  .body__header nav {
    width: 30%;
  }
}
.body__header ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}
@media (min-width: 768px) {
  .body__header ul {
    flex-wrap: unset;
  }
}
.body__header ul li {
  width: 50%;
  box-sizing: border-box;
  padding: 1rem;
}
.body__header ul li a {
  color: #ffffff;
  text-decoration: none;
}

footer {
  background-color: #323232;
  box-sizing: border-box;
  margin-top: 1rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  footer {
    padding: 0 2.25rem;
    font-size: 0.625rem;
  }
}
footer img {
  margin: 1rem;
}
footer #footer__container {
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
  footer #footer__container {
    align-content: center;
    flex-direction: row;
    justify-content: flex-start;
  }
}
@media (min-width: 768px) {
  footer #footer__container #footer__bandsite {
    text-align: right;
    order: -1;
    flex: 0 0 10%;
  }
}
@media (min-width: 768px) {
  footer #footer__container #footer__bandsite {
    font-size: 2rem;
    text-transform: uppercase;
    display: flex;
    align-items: center;
  }
  footer #footer__container #footer__bandsite p {
    font-size: 1.0625rem;
  }
}
@media (min-width: 768px) {
  footer #footer__primary-bold {
    font-family: "AvenirNextLTPro-Demi";
    display: flex;
    justify-content: flex-start;
    padding: 0;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
  }
  footer #footer__primary-bold p {
    font-size: 1.7rem;
  }
}
@media (min-width: 768px) {
  footer #footer__container > div:first-child {
    flex: 0 0 70%;
    order: -2;
  }
}

@media (min-width: 768px) {
  .footer__mini-container {
    width: calc(33% - 3rem);
  }
}

.main__hero-bio {
  background-size: cover;
  box-shadow: inset 0 0 0 1000px rgba(50, 50, 50, 0.35);
  background-image: url(../assets/Images/hero-bio.jpg);
  background-position: right;
  height: 45vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-content: center;
  flex-wrap: wrap;
}
.main__hero-bio h1 {
  margin-left: 2rem;
  color: #ffffff;
}

.main__biography {
  box-sizing: border-box;
  padding: 1rem;
  width: 100%;
}

.main__hidden {
  display: none;
}
@media (min-width: 768px) {
  .main__hidden {
    padding: 0 6rem;
    display: flex;
    flex-wrap: wrap;
  }
}
.main__hidden article {
  padding: 1rem;
  padding-bottom: 2rem;
  width: calc(50% - 2rem);
}
.main__hidden h2 {
  padding: 0 1rem;
  width: 100%;
}

@media (min-width: 768px) {
  .section__top {
    padding: 6rem;
    padding-bottom: 0rem;
  }
}

.main__gallery {
  display: flex;
  box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #fafafa;
}
@media (min-width: 768px) {
  .main__gallery {
    padding: 1.5rem;
    gap: 1.5rem;
  }
  .main__gallery h2 {
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .main__gallery {
    padding: 2rem 6rem;
  }
}
.main__gallery img {
  box-sizing: border-box;
  width: calc(100% - 2rem);
}
@media (min-width: 768px) {
  .main__gallery img {
    width: calc(33% - 1.35rem);
  }
}