// @use 'partials/mixins' as *;


@font-face {
  font-family: "AvenirNextLTPro-Regular";
  src: url("../assets/font/AvenirNextLTPro-Regular.otf") format("opentype");
}

@font-face {
  font-family: "AvenirNextLTPro-Demi";
  font-weight: 600;
  src: url("../assets/font/AvenirNextLTPro-Demi.otf") format("opentype");
}

@font-face {
  font-family: "AvenirNextLTPro-Bold";
  font-weight: 700;
  src: url("../assets/font/AvenirNextLTPro-Bold.otf") format("opentype");
}

$tablet-width: 768px;
$desktop-width: 1200px;

@mixin tablet {
  @media (min-width: $tablet-width) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: $desktop-width) {
    @content;
  }
}



$pri-dark: #323232;
$pri-mid: #afafaf;
$pri-light: #ffffff;

$sec-dark: #000000;
$sec-mid: #e1e1e1;
$sec-light: #fafafa;

img {
  max-width: 100%;
}

body {
  font-family: "AvenirNextLTPro-Regular";
  padding: 0;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

h2 {
  color: $sec-dark;
}

.body__header {
  width: 100%;
  background-color: $pri-dark;
  display: flex;
  align-items: center;

  @include tablet {
    width: auto;
    padding-left: 4rem;
    font-size: 1.7rem;
    align-content: space-between;
    flex-direction: row;
  }

  #navbar-logo {
    order: -1;
    width: 100%;
    color: $pri-light;
    font-size: 1.5rem;
    text-align: center;
    padding: 1rem;
    margin-right: auto;
    text-transform: uppercase;
    padding-top: 2rem;
    padding-bottom: 0;

    @include tablet {
      padding-top: 1rem;
      padding-bottom: 1rem;
    }
  }

  #navbar-active {
    border-style: solid;
    border-width: 0;
    border-bottom-width: 0.25rem;
    border-color: $pri-light;
  }

  nav {
    width: 100%;

    @include tablet {
      width: 30%;
    }
  }

  ul {
    list-style-type: none;
    // width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;

    @include tablet {

      flex-wrap: unset;
    }
  }

  ul li {
    width: 50%;
    box-sizing: border-box;
    padding: 1rem;
  }

  ul li a {
    color: $pri-light;
    text-decoration: none;
  }
}

footer {
  background-color: $pri-dark;
  box-sizing: border-box;
  // padding: 1rem;
  margin-top: 1rem;
  color: $pri-light;

  @include tablet {
    padding: 0 2.25rem;
    // measured
    font-size: 0.625rem;
    // measured
  }

  @include desktop {
    // padding: 2rem 6.5rem;
  }

  img {
    margin: 1rem;
  }

  #footer__container {
    display: flex;
    align-content: flex-start;
    flex-wrap: wrap;
    flex-direction: column;

    @include tablet {
      align-content: center;
      flex-direction: row;
      justify-content: flex-start;
    }

    @include desktop {}

    #footer__bandsite {

      @include tablet {
        text-align: right;
        order: -1;
        flex: 0 0 10%;
      }
    }

    // div {
    //   @include tablet {
    //     padding: 0 1rem;
    //   }

    //   @include desktop {
    //     padding: 0 1rem;
    //   }
    // }
    #footer__bandsite {
      @include tablet {
        font-size: 2rem;
        text-transform: uppercase;
        display: flex;
        align-items: center;

        p {
          font-size: 1.0625rem;
          // measured
        }
      }
    }

    div {
      @include tablet {
        // padding: 1rem 2rem;
      }
    }

  }

  #footer__primary-bold {
    @include tablet {
      font-family: 'AvenirNextLTPro-Demi';
      display: flex;
      justify-content: flex-start;
      padding: 0;
      flex-wrap: wrap;
      flex-direction: row;
      align-items: center;
      // padding-left: 1.5rem;

      p {
        font-size: 1.7rem;
        // measured
      }
    }
  }


  @include tablet {
    #footer__container>div:first-child {
      // order: -1;
      flex: 0 0 70%;
      order: -2;
    }
  }

}

.footer__mini-container {
  @include tablet {
    width: calc(33% - 3rem);
    // margin-right: 3rem;
  }
}